<template>
    <v-row>
        <v-col cols="12" offset-sm="3" offset-md="4" sm="6" md="4">
            <v-card>
                <v-toolbar dark color="primary">
                    <v-toolbar-title>Login</v-toolbar-title>
                </v-toolbar>
            </v-card>
            <v-card-text>
                <v-form @submit.prevent="submit" id="login-form" ref="form" >
                    <v-text-field prepend-icon="mdi-at" name="email" label="E-mail" type="text"
                                  v-model="email" required :rules="emailRules" ></v-text-field>
                    <v-text-field prepend-icon="mdi-textbox-password" name="password" label="Password" id="password"
                                  :type="showPass ? 'text' : 'password'"
                                  :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
                                  @click:append="showPass = !showPass"
                                  v-model="password" required :rules="passwordRules"></v-text-field>
                    <v-spacer></v-spacer>
                    <v-btn :loading="loading"
                           :disabled="loading"
                           ref="goButton"
                           color="primary" type="submit" form="login-form"
                    >Login
                    </v-btn>
                </v-form>
            </v-card-text>
        </v-col>
        <v-col cols="12" offset-sm="3" offset-md="4" sm="6" md="4">
            <v-alert type="error"
                     transition="scale-transition"
                     :value="showError"
            >{{errorMsg}}</v-alert>
        </v-col>
    </v-row>
</template>

<script>
    import store from '../store'

    export default {
        name: "Login",
        computed: {
            passwordRules() {
                return [
                    (v) => !!v || 'Password is required',
                    (v) => v.length >= 6 || 'Password must be 6 characters or greater'
                ]
            },
            emailRules() {
                return [
                    (v) => !!v || 'E-mail is required',
                    (v) => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
                ]
            },
            showError() {
                return this.errorMsg !== ''  //simplified
            }
        },
        data() {
            return {
                email: '',
                password: '',
                showPass: false,
                loading: false,
                errorMsg: '',
            }
        },
        methods: {
            submit() {
                if (this.$refs.form.validate()) {
                    this.loading = true
                    let data = {
                        email: this.email,
                        password: this.password
                    }

                    store.dispatch('login', data).then(() => {
                        this.$router.push({name: 'home'})
                    }).catch(errMsg => {
                        this.errorMsg = errMsg
                        this.loading = false
                    })
                }
            }
        }
    }
</script>

